@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* THEME */
:root {
    --header: rgb(60, 61, 68);
    --hover: rgb(70, 71, 78);
    --primary: rgb(48, 49, 54);
    --background: rgb(27,27,29);
    --input:rgb(46, 47, 51);
    --secondary: rgb(36, 37, 41);
    --accent: rgba(52, 152, 219,50%);
    --accent2: rgb(52, 152, 219);
    --red: rgb(192, 57, 43);
    --text: rgb(255,255,255);
    --text2: rgb(200,200,200)
    
    
}

/* MAIN */

*{
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

html, body, #root, .App{
    min-height: 100vh;
    background-color: rgb(27,27,29);
    background-color: var(--background);
    color: rgb(255,255,255);
    color: var(--text);
    margin: 0;
    padding: 0;
}

/* MAIN */
.header {
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  top: 0;
  z-index: 2;
  background-color: var(--header);
  height: 65px;
}

/* STEAM LOGIN */
.login-btn {
  position: absolute;
  color: var(--text);
  right: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: var(--primary);
  transition: 0.5s;
  width: 140px;
  height: 48px;
  top: 50%;
  border-radius: 8px;
  transform: translateY(-50%);
}

.steam-login-img {
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
}
.steam-login-text {
  pointer-events: none;
  position: absolute;
  font-size: 1rem;
  font-weight: 600;
  top: 50%;
  left: 50px;
  transform: translateY(-50%);
}

.login-btn:hover {
  background-color: var(--secondary);
  color: var(--accent);
  transition: 0.5s;
}

/* PROFILE */
.profile-btn{
  position: absolute;
  color: var(--text);
  right: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  background-color: var(--accent2);
  transition: 0.5s;
  width: 42px;
  height: 42px;
  top: 50%;
  border-radius: 50%;
  transform: translateY(-50%);
}

.sb-avatar{
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
}

.profile-menu{
  z-index: 1;
  display: none;
  position: absolute;
  width: 350px;
  height: 200px;
  top: 65px;
  right: 10px;
  background-color: var(--secondary); 
  border-bottom: 1px solid var(--hover);
  border-left: 1px solid var(--hover);
  border-right: 1px solid var(--hover);
}

.profile-menu.active{
  display: flex;
  flex-direction: column;
}

.profile-info{
  position: relative;
  background-color: var(--secondary);
  top: 0;
  width: 100%;
  height: 65px;
}

.profile-avatar{
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.profile-name{
  position: absolute;
  top: 20%;
  color: var(--text);
  left: 60px;
  font-size: 1.2em;
  transform: translateY(-20%);
}

.profile-rcoins{
  position: absolute;
  top: 80%;
  left: 60px;
  font-size: 1em;
  color: var(--text2);
  transform: translateY(-80%);
}

.button-container{
  background-color: var(--secondary);
  position: relative;
  display: flex;
  height: calc(100%-65px);
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
}

.button-container a{
  position: relative;
  width: 100%;
  height: 50px;
  color: var(--text);
  outline: none;
  border: none;
  background-color: var(--secondary);
  cursor: pointer;
}

.button-container a:hover{
  background-color: var(--hover);
  color: var(--accent2);
} 

.button-container a img{
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
}

.button-container a label{
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 60px;
  transform: translateY(-50%);
}






/* LOGO */
.rgngs-logo {
  position: absolute;
  height: 80px;
  top: 50%;
  left: 5px;
  width: 300px;
  transform: translateY(-50%);
}

@media screen and (max-width: 400px){
  .rgngs-logo{
    height: 60px;
    width: 200px;
  }
}



/* MAIN CODE */
 .footer {
  z-index: 1;
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  bottom: auto;
  padding: 10px;
  height: 80px;
  width: 100%;
  background-color: var(--secondary);
  border-top: solid 2px var(--accent);
  color: white;
  text-align: center;
 }


/* LOGO */
.footer-logo-container{
  display: flex;
  width: 30%;
  height: auto;
  align-items: center;
  justify-content: flex-start;
}
.footer-logo{
  margin-left: 15px;
}
/* INFO */
.footer-info-container{
  display: flex;
  height: auto;
  color: var(--text2);
  align-items: center;
  justify-content: center;
  margin: 5px 5px 5px 5px;
  width: 50%;
  font-size: 0.9em;
}

/* LINKS */
.footer-links-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  width: 30%;
 }

.footer-links-container li,a{
  list-style: none;
  margin-right: 10px;
}

.footer-links-container li a, a{
  color: var(--text);
  text-decoration: none;
  font-size: 1rem;
  text-transform: uppercase;
}

.footer-links-container li a:hover{
  color: var(--accent);
}

.footer-links-container  a:hover{
  color: var(--accent);
}


 @media screen and (max-width: 1100px){
  .footer{
    flex-direction: column;
    height: auto;
  }
  .footer-info-container{
    margin-top: 20px;
    width: auto;
  }
  .footer-logo-container{
    align-items: center;
    justify-content: center;
  }

  .footer-links-container{
    margin-top: 20px;
    width: auto;
    align-items: center;
    justify-content: center;
  }
}




.item-main{
    position: relative;
    background-color: var(--secondary);
    border-radius: 8px;
    width: 450px;
    height: 520px;
}

.item-header{
    position: relative;
    width: 100%;
    height: 50px;
    background: linear-gradient(to right, rgb(195, 55, 100), rgb(29, 38, 113));
    border-top-right-radius: 8px;
    border-top-left-radius: 8px;
}

.item-title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 1.5rem;
    text-transform: uppercase;
}

.item-button{
    position: absolute;
    bottom: 15px;
    left: 50%;
    background-image: linear-gradient(to right, rgb(195, 55, 100), rgb(29, 38, 113));
    width: 200px;
    height: 50px;
    border-radius: 25px;
    outline: none;
    border: none;
    transform: translateX(-50%);
    color: var(--text);
    font-size: 1.8rem;
}

.item-button:hover{
    cursor: pointer;
    background-position: right center;
}

.item-price-div{

    position: relative;
    width:100%;
    height: 50px;
}
.item-price{
    position: absolute;
    color: var(--text);
    left: 50%;
    padding-top: 15px;
    top: 50%;
    transform: translate(-50%,-50%);
    font-size: 2rem;
}

.item-content{
    margin-top: 10px;
    overflow: auto;
    position: relative;
    width: 100%;
    height: calc(100% - 180px);
    color: var(--text2);
    padding: 15px;
    white-space: pre;
}


::-webkit-scrollbar {
    width: 10px;

  }
  
  ::-webkit-scrollbar-track {
    background: var(--primary);
    border-radius: 8px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: var(--header);
    border-radius: 8px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: var(--hover);
  }

  @media screen and (max-width: 500px){
    .item-main{
     width: 250px;
    }

    .item-price{
        font-size: 1.5em;
    }
   }
   
.shop-container{
 min-height: calc(100vh - 64px);
 width: 100%;
}

.shop-text-container{
 margin-bottom: 30px;
 text-align: center
}

.shop-text-container h2{
 font-weight: 500;
 margin-bottom: 5px;
 font-size: 2.5rem;
}

.shop-desccription{
 position: relative;
 width: 40%;
 left: 50%;
 transform: translateX(-50%);
 height: auto;
 color: var(--text2);
}

.items-container{
 margin-bottom: 30px;
 position: relative;
 display: grid;
 left: 50%;
 transform: translateX(-50%);
 grid-template-columns: repeat(auto-fill, minmax(480px, 1fr));
 grid-row-gap: 50px;
 row-gap: 50px;
 align-content: center;
 justify-items: center;
 justify-content: space-between;
}


@media screen and (max-width: 500px){
 .items-container{
  justify-items: center;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));

 }
}



